.icon-facet-pill:hover .icon-facet-pill-affordance,
.icon-facet-pill:active .icon-facet-pill-affordance {
  color: var(--fa-red);
}
.icon-facet-pill-clear {
  --button-transition-duration: var(--timing-xfast);

