    opacity: 0.8;
  }
  60% {
    transform: translateY(4px);
    opacity: 1;
  }
  80% {
    transform: translateY(-2px);
  }
  100% {
    transform: translateY(0);
  }
}
@keyframes slideBounceFadeUp {
  0% {
    transform: translateY(0);
    opacity: 1;
  }
  30% {
    transform: translateY(-4px);
  }
  60% {
    transform: translateY(2px);
  }
  100% {
    transform: translateY(-28px);
    opacity: 0.8;
  }
}
